body {
  background: linear-gradient(to bottom, #d3ffd3 0%, white 100%);
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.custom-border {
  border-width: 2px !important;
  border-color: #74b174 !important;
}

.card-img-top {
  width: 100%;
  height: auto;
  object-fit: contain;
}

@media (max-width: 768px) {
  .card-img-top {
    max-height: 320px;
  }

  .card h5 {
    font-size: 16px;
  }

  .card p {
    font-size: 14px;
  }

  .col-4 {
    padding-left: 8px;
    padding-right: 8px;
  }

  .card-body {
    padding: 8px;
  }

  .download-cta p {
    padding-top: 10px;
    padding-bottom: 10px;

  }
}